import React from 'react';
import {} from "@mui/material";
import { BarChart, SearchRounded, ShoppingCartRounded } from "@mui/icons-material";

function Header() {
  return (
    <header>
        <img src="https://thumbs.dreamstime.com/b/chef-head-avatar-black-white-vector-illustration-graphic-design-143409152.jpg"
        alt=""
        className="logo"
        />

        <div className="inputBox">
            <SearchRounded className="searchIcon" />
            <input type="text" placeholder="Search" />
        </div>

        <div className="shoppingCart">
        <ShoppingCartRounded className="cart" />
            <div className="cart_content">
            <p>2</p>
            </div>
        </div>

        <div className="profileContainer">
            <div className="imgBox">
            <img 
                src="https://thumbs.dreamstime.com/b/chef-head-avatar-black-white-vector-illustration-graphic-design-143409152.jpg" alt=""
            />
            </div>
        <h2 className="userName">Soufian</h2>
        </div>
        <div className="toggleMenu">
        <a href=''>Sign Up</a>
        </div>
        <div className="toggleMenu">
        <a href=''>Sign In</a>
        </div>

    </header>
  );
}

export default Header;