import React, {useState}from 'react';
import axios from 'axios';
import jwtDecode from "jwt-decode";
import { useDispatch } from "react-redux";
import { loginAction , setRoleAction , setIdAction } from "../actions/authActions";
// import {Link} from 'react-router-dom'
const SingIn = () => {

 const [data , setData] = useState({
  email : "",
  password : ""
});

  const [submitted, setSubmitted] = useState(false);

  const handleEmail = (e) => {
    setData({ ...data, email: e.target.value });
  };
  const dispatch = useDispatch();

  const login =(email, password)=> {
   return axios.post(`http://localhost:3030/api/signin`, {
     email: email,
     password: password,
   });
 }

  const handlePassword = (e) => {
    setData({ ...data,password: e.target.value });
  };

  const handleSubmit =()=> {
    login( data.email, data.password).then((response) => {
      // window.localStorage.setItem("token", response.data.accessToken);
      // window.location = '/owners'
      (async () => {
        console.log(response.data.token)
        await dispatch(loginAction());
        await dispatch(setRoleAction(jwtDecode(response.data.token).role));
        await dispatch(setIdAction(jwtDecode(response.data.token)._id));
        // window.location = '/Dashboard'
      })()
    });
    setSubmitted(true);
  };

 return ( 
  <div className="container mx-auto">
  <div className="flex justify-center px-6 my-12">
   <div className="w-full xl:w-3/4 lg:w-11/12 flex">
    <div
     className="w-full h-auto bg-gray-400 hidden bg-blend-darken lg:block lg:w-5/12 bg-cover rounded-l-lg"
     style={{backgroundImage: 'url(./img/el-oumnia-puerto-hôtel-1414753-1.jpg)'}} 
    ></div>

    <div className="w-full lg:w-7/12 bg-white p-5 rounded-lg lg:rounded-l-none">
     <h3 className="pt-4 text-2xl text-center">Create an Account!</h3>
     <form className="px-8 pt-6 pb-8 mb-4 bg-white rounded"  onSubmit={(e) => {
                e.preventDefault();
                handleSubmit();
              }}>
      <div className="mb-4 md:flex md:justify-between">
      </div>
      <div className="mb-4">
       <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="email">
        Email
       </label>
       <input
        className="w-full px-3 py-2 mb-3 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
        id="email"
        type="email"
        placeholder="Email"
        email={data.email}
        onChange={handleEmail}
       />
      </div>
      <div className="mb-4 md:flex md:justify-between">
       <div className="mb-4 md:mr-2 md:mb-0">
        <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="password">
         Password
        </label>
        <input
         className="w-full px-3 py-2 mb-3 text-sm leading-tight text-gray-700 border border-red-500 rounded shadow appearance-none focus:outline-none focus:shadow-outline"
         id="password"
         type="password"
         placeholder="******************"
         password={data.password}
         onChange={handlePassword}
        />
        <p className="text-xs italic text-red-500">Please choose a password.</p>
       </div>
      </div>
      <div className="mb-6 text-center">
       <button
        className="w-full px-4 py-2 font-bold text-white bg-blue-500 rounded-full hover:bg-blue-700 focus:outline-none focus:shadow-outline"
        type="submit"
       >
        login Account
       </button>
      </div>
      <hr className="mb-6 border-t" />
      <div className="text-center">
       <a
        className="inline-block text-sm text-blue-500 align-baseline hover:text-blue-800"
        href="#"
       >
        Forgot Password?
       </a>
      </div>
      <div className="text-center">
      </div>
     </form>
    </div>
   </div>
  </div>
 </div>
  );
}
 
export default SingIn;