import React from "react";

import Header from "./Header";
import MenuContainer from "./MenuContainer";
import BannerName from "./BannerName";
import SubMenuContainer from "./SubMenuContainer";
import {
    AccountBalanceWalletRounded,
    Chat,
    Favorite,
    HomeRounded,
    Settings,
    SummarizeRounded,
  } from "@mui/icons-material";

function Home() {
    return(
<div className="App">
<Header />

      {/* Main Container */}

      <main>
        <div className="mainContainer">
          
          {/* Banner */}
          <div className="banner">
            <BannerName name={"Soufian"} discount={"20"} link={"#"} />
            <img
             src="https://thumbs.dreamstime.com/b/delivery-design-over-black-background-illustration-47532275.jpg"
             
              className="deliveryPic"
            />
          </div>
          {/* dishContainer*/}
          <div className="dishContainer">
            <div className="menuCard">
              <SubMenuContainer name={"Menu Category"} />
            </div>
            <div className="rowContainer"></div>
            <div className="dishitemContainer"></div>
          </div>
        </div>
        <div className="rightMenu"></div>
      </main>

      {/* Bottom Menu */}
      <div className="bottomMenu">
        <ul id="menu">
          <MenuContainer link={"#"} icon={<HomeRounded />} isHome />
          <MenuContainer link={"#"} icon={<Chat />} />
          <MenuContainer link={"#"} icon={<AccountBalanceWalletRounded />} />
          <MenuContainer link={"#"} icon={<Favorite />} />
          <MenuContainer link={"#"} icon={<SummarizeRounded />} />
          <MenuContainer link={"#"} icon={<Settings />} />
          <div className="indicator"></div>
        </ul>
      </div>
      </div>
      )
}
export default Home;